'use client'
import { useAuth } from "@/components/AuthContext" 
import { useEffect } from "react"
import { useRouter } from "next/navigation"

export default function Page(){
    const {isAuthenticated} = useAuth()
    const router = useRouter()


    useEffect(()=>{
       if(!isAuthenticated){
            router.push('/login')
        }
    },[isAuthenticated])
    return(
        <>
            <h1>Page</h1>
        </>
    )
}